<template>
  <div class="card">
    <div class="card-header">
      Exam01AsyncControl
    </div>
    <div class="card-body">
      <div>
        <button @click="handleBtn1" class="btn btn-outline-primary btn-sm mr-2">비동기 작업1</button>
        <button @click="handleBtn2" class="btn btn-outline-primary btn-sm mr-2">비동기 작업2</button>
      </div>
    </div>

    <div v-if="!loading" class="mt-3">
      <div>result : {{result}}</div>
    </div>

    <div v-if="loading" class="mt-3 spinner-border text-info" role="status">
      <span class="sr-only">Loading</span>
      
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";

const result = ref("");
const loading = ref(false);

function handleBtn1(){
  loading.value = true;
  work()
  .then((data)=>{
    result.value = data;
  })
  .catch((error)=>{})
  .finally(() =>{
    loading.value = false;
  });
}

async function handleBtn2(){
  try{
    //async를구매하려면 이걸 사용해야!
    loading.value = true;
    const data = await work();
    result.value = data;
    await work();
  }catch(error){
    console.log("");
  }finally{
    loading.value = false;
  }
}

//axios 함수가 아래와 같이 내부적으로 promise를 만들어서... 암튼 이렇게 만들어져 있음
function work(){
  const promise = new Promise((resolve, reject)=>{
    setTimeout(() => {
      resolve("value");
    }, 3000);
  })
  return promise;
}
</script>

