import axios from "axios";
import apiAuth from "@/apis/auth";

//게시물 목록 요청
async function getBoardList(pageNo=1){ //비동기 함수로 생성(pageNo=1은 pageNo가 넘어오지 않으면 default로 1로 설정하겠다!)
  let response = null;
  try{
    //쿼리 스트링으로 백엔드로 넘어감(params안에 넣어서)
    response = await axios.get("/board/list", {params:{pageNo}});
    return {result : "success", data : response.data};
  }catch(error){
    console.log(error);
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        //얘가 true일 경우에만...
        if(await apiAuth.refreshToken()){
          //여기에 getBoardList()로 해서 재귀함수로 호출해도 되지만, 권장하지 않음!
          response = await axios.get("/board/list", {params:{pageNo}});
          return {result : "success", data : response.data};

          // 이런방식으로 하지마...비동기안에서 동기...
          // await getBoardList();
        }
      }
    }else{
      //네트워크 오류
      return {result: "fail-netork"};
    }
  }
  if(response != null){
    return response.data; //data는 list의 return..! 즉 json!(불러온거!)
  }else{
    return null;
  }
}

//게시물 쓰기(멀티 파트로 구성해서 전달)
async function createBoard(multipartFormData){
  let dbBoard = null;
  try{
    const response = await axios.post("/board/", multipartFormData);
    dbBoard = response.data;
  }catch(error){
    console.log(error);
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        //얘가 true일 경우에만...
        if(await apiAuth.refreshToken()){
          //여기에 getBoardList()로 해서 재귀함수로 호출해도 되지만, 권장하지 않음!
          const response = await axios.post("/board/", multipartFormData);
          dbBoard = response.data;
        }
      }
    }
  }
  return dbBoard;
}

//게시물 가져오기
async function readBoard(bno, hit){
  let board = null;
  try{
    const response = await axios.get(`/board/${bno}?hit=${hit}`);
    board = response.data;
  }catch(error){
    console.log(error);
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        //얘가 true일 경우에만...
        if(await apiAuth.refreshToken()){
          //여기에 getBoardList()로 해서 재귀함수로 호출해도 되지만, 권장하지 않음!
          const response = await axios.get(`/board/${bno}?hit=${hit}`);
          board = response.data;
        }
      }
    }
  }

  return board;
}

//파일 다운로드
async function downloadBoardAttach(bno){
  let blob = null;
  try{
    //다운로드받는건 정확하게 blob타입이야! 라는것을 알려주는 옵션을 추가!
    //그래야 blob에 저장할 수 있음~
    const response = await axios.get(`/board/battach/${bno}`, {responseType: "blob"});
    blob = response.data;
  }catch(error){
    console.log(error);
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        //얘가 true일 경우에만...
        if(await apiAuth.refreshToken()){
          //여기에 getBoardList()로 해서 재귀함수로 호출해도 되지만, 권장하지 않음!
          const response = await axios.get(`/board/battach/${bno}`, {responseType: "blob"});
          blob = response.data;
        }
      }
    }
  }
  return blob;
}

//게시물 삭제
async function deleteBoard(bno){
  let result = null;
  try{
    const response = await axios.delete(`/board/${bno}`);
    // result = "success";
    result = response.data.result;
  }catch(error){
    console.log(error);
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        //얘가 true일 경우에만...
        if(await apiAuth.refreshToken()){
          //여기에 getBoardList()로 해서 재귀함수로 호출해도 되지만, 권장하지 않음!
          const response = await axios.delete(`/board/${bno}`);
          // result = "success";
          result = response.data.result;
        }
      }
    }
  }
  return result;
}

//게시물 수정
async function updateBoard(multipartFormData){
  let dbBoard = null;
  try{
    const response = await axios.put("/board/", multipartFormData);
    dbBoard = response.data;
  }catch(error){
    console.log(error);
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        //얘가 true일 경우에만...
        if(await apiAuth.refreshToken()){
          //여기에 getBoardList()로 해서 재귀함수로 호출해도 되지만, 권장하지 않음!
          const response = await axios.put("/board/", multipartFormData);
          dbBoard = response.data;
        }
      }
    }
  }

  return dbBoard;
}

//내보내기
export default{
  getBoardList,
  createBoard,
  readBoard,
  downloadBoardAttach, 
  deleteBoard,
  updateBoard
}

