<template>
  <div class="card">
    <div class="card-header">
      Read
    </div>
    <div class="card-body">
      <div v-if="board != null">
        <div class="d-flex">
          <div>
            <p>bno : {{board.bno}}</p>
            <p>btitle : {{board.btitle}}</p>
            <p>bcontent : {{board.bcontent}}</p>
            <p>mid : {{board.mid}}</p>
            <p>bdate : {{new Date(board.bdate).toLocaleDateString()}}</p>
            <p>bhitcount : {{board.bhitcount}}</p>
            <p v-if="board.battachoname != null">
              battachoname : {{board.battachoname}}
              <button @click="downloadBattach" class="btn btn-outline-warning btn-sm ml-2">다운로드</button>
            </p>
          </div>
          <div class="ml-5">
            <img v-if="board.battachoname != null" :src="battach" width="300"/>
          </div>
        </div>
        <div>
          <router-link :to="`/menu07/board/list?pageNo=${pageNo}`" 
            class="btn btn-outline-info btn-sm mr-2">목록</router-link>
          <router-link :to="`/menu07/board/updateform?bno=${$route.query.bno}&pageNo=${$route.query.pageNo}`" 
            class="btn btn-info btn-sm mr-2">수정</router-link>
          <button class="btn btn-outline-danger btn-sm mr-2" @click="handleRemove">삭제</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useRoute, useRouter } from "vue-router";
import apiBoard from "@/apis/board";
import {ref} from "vue";

//npm install js-file-download
import fileDownload from "js-file-download";

const route = useRoute();
const router = useRouter();

const bno = route.query.bno;

//바뀌는 값이 아니므로 반응형속성으로 하지 않음
const pageNo = route.query.pageNo;
const hit = route.query.hit;

const board = ref(null);
const battach = ref(null);

async function getBoard(){
  //얘는 비동기처리가 되고, 바로 아래에 있는 await를 처리함!
  apiBoard.readBoard(bno, hit)
  .then((data =>{
    board.value = data;
  }));
  
  //바이너리 데이터
  const blob = await apiBoard.downloadBoardAttach(bno);
  if(blob != null){ //뭔가 있다면..!

  //바이너리 데이터를 막바로 넣을 수 없고 createObjectURL함수를 통해 blob을, 메모리에 있는 것을 마치 URL로 만들어서 저장!
  //그래야 바인딩이 가능함
    battach.value = URL.createObjectURL(blob);
  }
}
getBoard();

async function downloadBattach(){
  const blob = await apiBoard.downloadBoardAttach(bno);
  if(blob != null){
    fileDownload(blob, board.value.battachoname);
  }
}

async function handleRemove(){
  const result = await apiBoard.deleteBoard(bno);
  if(result === "success"){
    router.push(`/menu07/board/list?pageNo=${pageNo}`);
  }
}
</script>

